<template>
    <div class="converter-moedas">
        <h2> {{moeda1}} to {{moeda2}}</h2>
        <input type="number" v-model="moeda1_v" v-bind:placeholder="moeda1">
        <input type="button" class="btn" value="Converter" v-on:click="converter()">
        <h2>{{moeda2_v}}</h2>
    </div>

    <div class="authors">
        <h2>Authors</h2>
        <p>Karllisson Henrique <br>
        Lívia Beatriz <br>
        Maísa Lira</p>
    </div>
</template>

<script>
    export default{
        name: "converter-moedas",
        props: ['moeda1', 'moeda2'],
        data(){
            return{
                moeda1_v: '',
                moeda2_v: "$ " + 0
            }
        },
        methods: {
            converter (){
                let from_to = this.moeda1 + '_' + this.moeda2;
                let url = 'https://free.currconv.com/api/v7/convert?q='+ from_to +'&compact=ultra&apiKey=32062dbca959ea18b6ff'

                fetch(url).then(res => {
                    return res.json();
                    }).then(json => {
                        let cotacao = json[from_to];
                        this.moeda2_v = "$ " + (cotacao * parseFloat(this.moeda1_v)).toFixed(3);
                    })
            }
        }
    };
</script>

<style scoped>
    .converter-moedas{
        width: 400px;
        padding: 10px;
        box-shadow: 2px 4px 8px 2px rgba(0, 0, 0, 0.2);
    }

    .authors{
        margin-left: 10px;
        width: 400px;
        box-shadow: 2px 4px 8px 2px rgba(0, 0, 0, 0.2 );
    }
</style>>
